import { createRouter, createWebHashHistory } from 'vue-router';

export const routePathMap = {
  "/": "root",
  "/dashboard": "Dashboard",
  "/drivers": "Drivers",
  "/addDriver": "Add driver",
  "/rides": "Rides",
  "/drivers/edit": "Edit Driver",
  "/drivers/view": "Driver Profile",
  // "/tableComponent": "Table Component"
}

export const routes = [
  {
    path: '/',
    name: 'root',
    redirect: "dashboard",
    rootObj: true
  },
  {
    path: '/:pathMatch(.*)',
    name: 'wildcard',
    redirect: "/",
    rootObj: true
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: () => import(/* webpackChunkName: "about" */ '../views/Dashboard.vue'),
  },
  {
    path: '/drivers',
    name: 'Drivers',
    component: () => import(/* webpackChunkName: "about" */ '../views/ViewDrivers.vue')
  },
  {
    path: '/addDriver',
    name: 'Add driver',
    rootObj: true,
    component: () => import(/* webpackChunkName: "about" */ '../views/AddDrivers.vue') ,
  },
  {
    path: '/drivers/edit',
    name: 'Edit Driver1',
    settings: { hideInNav: true },
    redirect: "/drivers",
    rootObj: true
  },
  {
    path: '/drivers/edit/:driverId(\\d+)',
    name: 'Edit Driver',
    settings: { hideInNav: true },
    rootObj: true,
    props: true,
    component: () => import(/* webpackChunkName: "about" */ '../views/AddDrivers.vue'),
  },
  {
    path: '/drivers/view',
    name: 'View Driver1',
    settings: { hideInNav: true },
    redirect: "/drivers",
    rootObj: true
  },
  {
    path: '/drivers/view/:driverId(\\d+)',
    name: 'Driver Profile',
    settings: { hideInNav: true },
    rootObj: true,
    component: () => import(/* webpackChunkName: "about" */ '../views/DriverProfile.vue'),
  },
  {
    path: '/rides',
    name: 'Rides',
    // redirect: "/rides/view",
    component: () => import(/* webpackChunkName: "about" */ '../views/ViewRides.vue')
    // children: [
    //   {
    //     path: 'view',
    //     name: 'View',
    //     component: () => import(/* webpackChunkName: "about" */ '../views/ViewRides.vue'),
    //   }
    // ]
  }
  // {
  //   path: '/tableComponent',
  //   name: 'Table Component',
  //   // redirect: "/rides/view",
  //   component: () => import(/* webpackChunkName: "about" */ '@/admin/packages/table/TableDemo')
  // }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
